import React, { createContext, useEffect } from 'react';
import io from 'socket.io-client';
import { useDispatch } from 'react-redux';
import { updateMachineConfig } from './machine.actions';
import { MachineConfig } from './machine.types';

export const WebSocketProvider = ({ children }: any) => {
  const WebSocketContext = createContext<any>(null);
  let socket: any = null;
  let ws;

  const dispatch = useDispatch();

  useEffect(() => {
    if (!socket) {
      //@ts-ignore
      // eslint-disable-next-line react-hooks/exhaustive-deps
      socket = io('http://localhost:5000');

      socket.on('machineUpdater', (data: MachineConfig[]) => {
        // const payload = data.map((c) => ({
        //   id: c.id,
        //   name: c.equipmentHeader.OEMName,
        //   fuelUsed: { fuelConsumed: c.fuelUsed.fuelConsumed },
        // }));
        // console.log(data);
        dispatch(updateMachineConfig(data));
      });

      // eslint-disable-next-line react-hooks/exhaustive-deps
      ws = {
        socket: socket,
      };
    }
    return () => socket.disconnect();
  }, [socket]);

  return (
    <WebSocketContext.Provider value={ws}>{children}</WebSocketContext.Provider>
  );
};
